@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@900&display=swap);
.form {
  display: flex;
  justify-content: center;
  margin: 50px 0;
}

.input {
  background: #e0dede;
  color: black;
  width: 240px;
  height: 40px;
  border: none;
  border-radius: 20px;
  border: 2px solid #251f44;
  margin-right: 20px;
  padding: 6px;
}

.input:focus {
  outline: none;
}

.btn {
  background-color: #14b1ab;
  color: white;
  width: 100px;
  height: 40px;
  border: none;
  border-radius: 20px;
  padding: 6px;
  font-size: 16px;
  box-sizing: content-box;
  border: 2px solid #251f44;
  transition: 0.5s;
  cursor: pointer;
}

.btn:focus {
  outline: none;
}

.btn:hover {
  background-color: #0f8581;
  color: black;
}

.error-msg {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
}

body {
    padding: 0;
    margin: 0;
    background: #e4e3e3;
    font-family: 'Poppins', sans-serif;
    color: #005451;
}

p {
    position: absolute;
    font-size: 12px;
}

.card {
    position: absolute;
    height: 230px;
    width: 450px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #14b1ab;
    border-radius: 20px;
    box-shadow: 0 0 10px -2px black;
    text-align: center;
    overflow: hidden;
}

.line {
    position: absolute;
    left: 34%;
    height: 66%;
    width: 1px;
    background-color: #333333;
}

.blog {
    position: absolute;
    left: 10px;
    top: 10px;
}

.name {
    display: inline-block;
    margin: 0;
    color: #1d1b38;
    text-transform: uppercase;
}

.repo {
    left: 50px;
    bottom: 10px;
    left: 10px;
}

.company {
    left: 50px;
    bottom: -4px;
    left: 10px;
}

.avatar {
    width: 120px;
    height: auto;
    border-radius: 50%;
    position: absolute;
    left: 16px;
    top: 20%;
    box-sizing: border-box;
    border: 2px solid #00fff5;
}

.place{
    left: 36%;
}

.joined-date {
    right: 10px;
}

.bio {
    width: 60%;
    left: 36%;
    top: 35%;
    font-size: 16px;
    color: #141414;
}

.fa-users, .fa-twitter {
    color: #000;
    font-size: 24px;
}

.fa-twitter {
    font-size: 32px;
}

.followers {
    left: 36%;
    bottom: -10px;
    font-size: 16px;
}

.twitter {
    bottom: -10px;
    right: 10px;
}
